/* Micro clearfix hack for clearing floats - h5bp.com/q */
@mixin clearfix {
  *zoom: 1;
  &:before, &:after {
    display: table;
    content: " ";
  }
  &:after {
    clear: both;
  }
}

.clearfix {
  @include clearfix;
}

/* Image replacement */
.ir {
  display: block;
  border: 0;
  text-indent: -999em;
  overflow: hidden;
  background-color: transparent;
  background-repeat: no-repeat;
  text-align: left;
  direction: ltr;
}

/* Hide from both screenreaders and browsers: h5bp.com/u */
.hidden {
  display: none !important;
  visibility: hidden;
}

/* Hide only visually, but have it available for screenreaders: h5bp.com/v */
.vh {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}

/* Extends the .visuallyhidden class to allow the element to be focusable when navigated to via the keyboard: h5bp.com/p */
.vh.focusable:active, .vh.focusable:focus {
  clip: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  position: static;
  width: auto;
}

/* Hide visually and from screenreaders, but maintain layout */
.invisible {
  visibility: hidden;
}

@mixin retina-bg($bg, $bg2x) {
  background-image: url($bg);
  @media (-webkit-min-device-pixel-ratio: 2),
  (min-resolution: 192dpi) {
    background-image: url($bg2x);
  }
}


/* User select */
@mixin user-select ($val: none) {
  -webkit-touch-callout: $val;
  -webkit-user-select: $val;
  -khtml-user-select: $val;
  -moz-user-select: $val;
  -ms-user-select: $val;
  user-select: $val;
}

// Scale
@mixin zoom($ratio: 1) {
  zoom: $ratio;
  -moz-transform: scale($ratio);
  -moz-transform-origin: 0 0;
}

// Appearance
@mixin appearance($appearance: none) {
  -webkit-appearance: $appearance;
  -ms-appearance: $appearance;
  -o-appearance: $appearance;
  appearance: $appearance;
}

// Placeholder
@mixin placeholder($color: $grayLight) {
  ::-webkit-input-placeholder {
    color: $color;
  }
  ::-moz-placeholder {
    color: $color;
  }
  :-moz-placeholder {
    color: $color;
  }
  :-ms-input-placeholder {
    color: $color;
  }
}

// Text Overflow
@mixin text-truncate {
  text-overflow: ellipsis;
  vertical-align: top;
  white-space: nowrap;
  overflow: hidden;
  display: inline-block;
}

// Border Radius
@mixin border-radius($radius: 5px) {
  border-radius: $radius;
}

@mixin border-top-radius($radius) {
  border-top-right-radius: $radius;
  border-top-left-radius: $radius;
}

@mixin border-right-radius($radius) {
  border-bottom-right-radius: $radius;
  border-top-right-radius: $radius;
}

@mixin border-bottom-radius($radius) {
  border-bottom-right-radius: $radius;
  border-bottom-left-radius: $radius;
}

@mixin border-left-radius($radius) {
  border-bottom-left-radius: $radius;
  border-top-left-radius: $radius;
}

// Drop shadows
@mixin box-shadow($shadow: 0 1px 3px rgba(0,0,0,.25)) {
  -webkit-box-shadow: $shadow;
  box-shadow: $shadow;
}

// Box
@mixin box($box: box) {
  display: block;
  display: -webkit-box;
  display: -moz-box;
  display: $box;
}

// Ordinal group
@mixin box-ordinal-group($order) {
  -webkit-box-ordinal-group: $order;
  -moz-box-ordinal-group: $order;
  box-ordinal-group: $order;
}

// Box orient
@mixin box-orient($orient: vertical) {
  -webkit-box-orient: $orient;
  -moz-box-orient: $orient;
  box-orient: $orient;
}

/* Flexbox */
@mixin flex-box($flex: flex) {
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -moz-flex;
  display: -webkit-flex;
  display: $flex;
}

/* Direction of the elements inside a flexbox */
/* row | row-reverse | column | column-reverse */
@mixin flex-direction($direction) {
  -webkit-flex-direction: $direction;
  flex-direction: $direction;
}

/* Order elements (int) inside a flexbox */
@mixin flexbox-order($order) {
  -webkit-box-ordinal-group: $order;
  -moz-box-ordinal-group: $order;
  -ms-flex-order: $order;
  -webkit-order: $order;
  order: $order;
}

/* Single line or multiple lines inside a flexbox */
/* nowrap | wrap | wrap-reverse */
@mixin flex-box-wrap($wrap: wrap) {
  -webkit-flex-wrap: $wrap;
  -ms-flex-wrap: $wrap;
  flex-wrap: $wrap;
}

@mixin flexbox-align($value) {
  -webkit-align-content: $value;
  -moz-align-content: $value;
  -ms-align-content: $value;
  align-content: $value;
}

@mixin flex($value) {
  -webkit-box-flex: $value;
  -moz-box-flex: $value;
  -webkit-flex: $value;
  -ms-flex: $value;
  flex: $value;
}

/* Length of the item relative to the rest of the flexible items */
@mixin flexbox-flex($width: 1) {
  -webkit-flex: 1;
  -ms-flex: 1;
  flex: 1;
}

/* Justify elements (how) inside a flexbox */
@mixin flexbox-justify($value) {
  -webkit-justify-content: $value;
  -moz-justify-content: $value;
  -ms-justify-content: $value;
  justify-content: $value;
}

@mixin align-content($align: stretch) {
  -webkit-align-content: $align;
  -ms-align-content: $align;
  align-content: $align;
}

@mixin justify-content($justify: flex-start) {
  -webkit-justify-content: $justify;
  -ms-justify-content: $justify;
  justify-content: $justify;
}

// Box orient
@mixin box-orient($orient: vertical) {
  -webkit-box-orient: $orient;
  -moz-box-orient: $orient;
  box-orient: $orient;
  flex-direction: row;
}

// Box sizing
@mixin border-box($sizing: border-box) {
  -moz-box-sizing: $sizing;
  -webkit-box-sizing: $sizing;
  box-sizing: $sizing;
  //*behavior: url('../js/polyfills/boxsizing.htc');
}

@mixin content-box($sizing: content-box) {
  -webkit-box-sizing: $sizing;
  -moz-box-sizing: $sizing;
  box-sizing: $sizing;
}

// Background size
@mixin background-size($size:cover) {
  -webkit-background-size: $size;
  -moz-background-size: $size;
  -o-background-size: $size;
  background-size: $size;
}

