// GPU Rendering
@mixin enable-webkit-gpu($perspective:1000) {
  /* enable gpu rendering (ios hack) */
  -webkit-transform: translateZ(0);
  transform: translateZ(0);

  @include backface-visibility;

  -webkit-perspective: $perspective;
  perspective: $perspective;
}

@mixin transform-style($style:preserve-3d) {
  -webkit-transform-style: $style;
  -moz-transform-style: $style;
  transform-style: $style;
}

@mixin backface-visibility($visibility:hidden) {
  -webkit-backface-visibility: $visibility;
  -moz-backface-visibility: $visibility;
  backface-visibility: $visibility;
}

@mixin perspective($perspective) {
  -webkit-perspective: $perspective;
  -moz-perspective: $perspective;
  perspective: $perspective;
}

@mixin perspective-origin($perspectiveOrigin) {
  -webkit-perspective-origin: $perspectiveOrigin;
  -moz-perspective-origin: $perspectiveOrigin;
  perspective-origin: $perspectiveOrigin;
}

// Background clipping
@mixin background-clip($clip) {
  -webkit-background-clip: $clip;
  -moz-background-clip: $clip;
  background-clip: $clip;
}

@mixin background-clip-border() {
  -moz-background-clip: border;
  -webkit-background-clip: border;
  background-clip: border-box;
}

// Alpha colors
@mixin background-color-with-alpha ($color, $alpha) {
  $rgba: rgba(red($color), green($color), blue($color), $alpha);
  /* background-color-with-alpha */ //
  background-color: $color;
  background-color: $rgba;
  // No longer using MS filters because they break border radius in IE9
  // $argb: argb($rgba);
  // filter: ~"progid:DXImageTransform.Microsoft.gradient(startColorstr=${argb}, endColorstr=${argb})";
  // -ms-filter: ~"progid:DXImageTransform.Microsoft.gradient(startColorstr=${argb}, endColorstr=${argb})";
}

@mixin border-color-with-alpha ($color, $alpha) {
  $rgba: rgba(red($color), green($color), blue($color), $alpha);
  /* border-color-with-alpha */ //
  border-color: $color;
  border-color: $rgba;
}

