// transform
@mixin transform($function) {
  transform: $function;
  -ms-transform: $function; /* IE 9 */
  -moz-transform: $function; /* Firefox */
  -webkit-transform: $function; /* Safari and Chrome */
  -o-transform: $function; /* Opera */
}

// transform origin
@mixin transform-origin($function) {
  transform-origin: $function;
  -ms-transform-origin: $function; /* IE 9 */
  -moz-transform-origin: $function; /* Firefox */
  -webkit-transform-origin: $function; /* Safari and Chrome */
  -o-transform-origin: $function; /* Opera */
}

// Filter
@mixin filter($filter) {
  filter: $filter;
  -o-filter: $filter;
  -ms-filter: $filter;
  -moz-filter: $filter;
  -webkit-filter: $filter;
}

// Opacity
@mixin opacity($opacity: 100) {
  opacity: $opacity / 100;
  @include backface-visibility;
}

// Optimize font
@mixin optimize-font($smoothing:antialiased) {
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: $smoothing;
  -moz-osx-font-smoothing: grayscale;
  font-smoothing: $smoothing;
}

// Font size
@mixin fs($size) {
  font-size: 0px + $size;
  font-size: 0rem + $size / $doc-font-size;
}

// Line height
@mixin lh($lh) {
  line-height: 0px + $lh;
  line-height: 0rem + $lh / $doc-font-size;
}

// =========================================================================
//
//  PRECISE CONTROL OVER RESPONSIVE TYPOGRAPHY FOR SASS
//  ---------------------------------------------------
//  Indrek Paas @indrekpaas
//
//  Inspired by Mike Riethmuller's Precise control over responsive typography
//  http://madebymike.com.au/writing/precise-control-responsive-typography/
//
//  `strip-unit()` function by Hugo Giraudel
//
//  11.08.2016 Remove redundant `&` self-reference
//  31.03.2016 Remove redundant parenthesis from output
//  02.10.2015 Add support for multiple properties
//  24.04.2015 Initial release
//
//  Usage:
//  @include fluid-type(font-size, 320px, 1400px, 18px, 45px);
//  @include fluid-type(padding-bottom padding-top, 20em, 70em, 2em, 4em);
//
// =========================================================================

@mixin fluid-type($properties, $min-vw, $max-vw, $min-value, $max-value) {
  @each $property in $properties {
    #{$property}: $min-value;
  }

  @media screen and (min-width: $min-vw) {
    @each $property in $properties {
      #{$property}: calc(#{$min-value} + #{strip-unit($max-value - $min-value)} * (100vw - #{$min-vw}) / #{strip-unit($max-vw - $min-vw)});
    }
  }

  @media screen and (min-width: $max-vw) {
    @each $property in $properties {
      #{$property}: $max-value;
    }
  }
}

@function strip-unit($value) {
  @return $value / ($value * 0 + 1);
}

/* Underline text */
@mixin underline-text ($textColor: black, $bgColor: white, $height:1px) {

  color: $textColor;
  //ie9 fallback
  text-decoration: underline;
  display: inline-block;

  text-decoration: none;
  text-shadow: 3px 0 $bgColor, 2px 0 $bgColor, 1px 0 $bgColor, -1px 0 $bgColor, -2px 0 $bgColor, -3px 0 $bgColor;

  &:after {
    margin-top: -8px;
    content:''; display: block; height: $height; width: 100%;
    background-color: $textColor;
    background-repeat: repeat-x;
    background-size: 1px $height;
    /*
    background-image: -webkit-linear-gradient($bgColor 20%, $textColor 100%);
    background-image: linear-gradient($bgColor 20%, $textColor 100%);
    background-position: 0 95%;

    @media (-webkit-min-device-pixel-ratio: 1.75), (min-resolution: 168dpi) {
    background-position: 0 93%;

  } */

  }

  &::selection {
    background-color: $selection;
    text-shadow: none;
    color: inherit;
  }

}

// Height
@mixin h($h) {
  height: 0px + $h;
  height: 0rem + $h / $doc-font-size;
}

// Margin
@mixin m($mt:0, $mr:0, $mb:0, $ml:0) {
  margin: (0px + $mt) (0px + $mr) (0px + $mb) (0px + $ml);
  margin: (0rem + $mt / $doc-font-size) (0rem + $mr / $doc-font-size) (0rem + $mb / $doc-font-size) (0rem + $ml / $doc-font-size);
}

@mixin mt($m) {
  margin-top: 0px + $m;
  margin-top: 0rem + $m / $doc-font-size;
}

@mixin mb($m) {
  margin-bottom: 0px + $m;
  margin-bottom: 0rem + $m / $doc-font-size;
}

// Retina
// A helper mixin for applying high-resolution background images (http://www.retinajs.com)
@mixin at2x($path, $at2x_path, $w: auto, $h: auto) {
  background-image: url($path);
  @media all and (-webkit-min-device-pixel-ratio: 1.5) {
    background-image: url($at2x_path);
    background-size: $w $h;
  }
}

// Selection
@mixin selection($background: $grayDark, $color: $white) {
  ::-moz-selection {
    background-color: $background;
    color: $color;
  }
  ::selection {
    background-color: $background;
    color: $color;
  }
}

// Animations
@mixin animate($name, $time: 4s, $iteration: infinite) {
  -webkit-animation: $name $time $iteration;
  -moz-animation: $name $time $iteration;
  -o-animation: $name $time $iteration;
  animation: $name $time $iteration;
}

@mixin animation-name($name) {
  animation-name: $name;
  -webkit-animation-name: $name;
}

@mixin animation-duration($duration: 2s) {
  animation-duration: $duration;
  -webkit-animation-duration: $duration;
}

@mixin animation-iteration-count($count: infinite) {
  animation-iteration-count: $count;
  -webkit-animation-iteration-count: $count;
}

// Pseudo element
@mixin pseudo-element($top:0, $left:0) {
  position: absolute;
  content: "";
  top: $top;
  left: $left;
}

// Columns
@mixin column-count($count:2) {
  -webkit-column-count: $count; /* Chrome, Safari, Opera */
  -moz-column-count: $count; /* Firefox */
  column-count: $count;
}

@mixin column-gap($gap:20px) {
  -webkit-column-gap: $gap; /* Chrome, Safari, Opera */
  -moz-column-gap: $gap; /* Firefox */
  column-gap: $gap;
}

@mixin column-break-inside-avoid {
  -webkit-column-break-inside: avoid; /* Chrome, Safari */
  page-break-inside: avoid; /* Theoretically FF 20+ */
  break-inside: avoid-column; /* IE 11 */
}

/* ------------------------ */
/* LESS mixin for CSS arrow */
/* ------------------------ */

/* Usage
 * .arrow(size, color, direction, offset, border-size, border-color);
 */

/* Where
 * Size is the with of the arrow
 * Color is the color of the arrow (plain color required)
 * Direction is the orientation of the arrow (top, right, bottom, left)
 * Offset is the position of the arrow on its axis (px / em)
 * Border-size is the width of the border if there is one (optional; default "0")
 * Border-color is the color of the border if there is one (optional; default "inherit");
 */

/* Extra
 * Drop-shadows can be used on the element to create a shadow on the arrow as well
 */
//
// @mixin arrow($size, $color, $direction, $offset) {
//   position: absolute;
//   content: "";
//   width: 0;
//   height: 0;
// }
//
// @mixin arrow($size, $color, $direction, $offset) when ($direction = top) {
//
//   left: $offset;
//   margin-left: -$size;
//
//   border-left: $size solid transparent;
//   border-right: $size solid transparent;
//   border-bottom: $size solid $color;
//
// }
//
// @mixin arrow($size, $color, $direction, $offset) when ($direction = bottom) {
//
//   left: $offset;
//   margin-left: -$size;
//
//   border-left: $size solid transparent;
//   border-right: $size solid transparent;
//   border-top: $size solid $color;
//
// }
//
// @mixin arrow($size, $color, $direction, $offset) when ($direction = right) {
//
//   left: 100%;
//   top: $offset;
//   margin-top: -$size;
//
//   border-top: $size solid transparent;
//   border-bottom: $size solid transparent;
//   border-left: $size solid $color;
//
// }
//
// @mixin arrow($size, $color, $direction, $offset) when ($direction = left) {
//
//   right: 100%;
//   top: $offset;
//   margin-top: -$size;
//
//   border-top: $size solid transparent;
//   border-bottom: $size solid transparent;
//   border-right: $size solid $color;
//
// }
